.sidedrawer{
    width: 70%;
    height: 100vh;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    transform: translateX(-100%);
    transition: all 0.3s ease-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.sidedrawer.show{
    transform: translateX(0);
}

.sidedrawer__links{
    display: flex;
    flex-direction: column;
    list-style: none;
}

.sidedrawer__links > li{
    display: flex;
    align-items: center;

}

.sidedrawer__links > li  > a{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex: 1;
    padding: 1rem;
    text-decoration: none;
    color: #171717;
    font-size: 1.3rem;
}

.sidedrawer__links > li  > a:hover{
    background-color: #171717;
    color: #f4f4f4;
}

.sidedrawer__links span{
    display: flex;
    align-items: center;
    margin-left: 5px;
}

.sidedrawer__login, .sidedrawer__orders{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sidedrawer__op1{
    font-size: 0.9rem;
    margin-bottom: 5px;
}

.sidedrawer__cartTotalProducts{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 6px 10px;
    border-radius: 50%;
    background-color: #171717;
    color: #f4f4f4;
    font-size: 1rem;
    margin-left: 5px;
}

.sidedrawer__links > li  > a:hover .sidedrawer__cartTotalProducts{
    background-color: #f4f4f4;
    color: #171717;
}

@media (min-width: 860px){
    .sidedrawer{
        display: none;
    }
}